import axios from 'axios';

export const checkMembership = async (nom, prenom) => {
  try {
    // Appel au proxy local, PAS directement à l'API Pep's Up
    const response = await axios.get('http://localhost:4000/api/pepsup', {
      params: { nom, prenom }
    });
    if (Array.isArray(response.data) && response.data.length > 0) {
      return response.data[0];
    } else {
      return null;
    }
  } catch (error) {
    throw new Error(error.response?.data?.message || "Pas de réponse du serveur - Vérifiez votre connexion");
  }
};